.wx-toast {
  position: absolute;
  z-index: 10;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 274rem;
  box-sizing: border-box;
  background-color: #4c4c4c;
  background-repeat: no-repeat;

  &.wx-toast--success {
    padding: 170rem 36rem 36rem 36rem;
    background-image: url(data:image/png;base64,);
    background-size: auto 46rem;
    background-position: center 72rem;
    border-radius: 34rem;
  }

  p {
    font-size: 26rem;
    color: #fff;
    display: -webkit-box; /* 设置为弹性盒子 */
    -webkit-box-orient: vertical; /* 设置为垂直排列 */
    -webkit-line-clamp: 2; /* 显示多少行文本 */
    overflow: hidden; /* 超出的部分隐藏 */
    text-overflow: ellipsis; /* 超出的部分显示省略号 */
    text-align: center;
  }
}
